
<template>
        <v-layout row class="text-xs-center fundo">
        <v-flex xs12 class="grey lighten-4">
            <v-container style="position: relative;top: 1%;" class="text-xs-center">
            <v-card flat>
                <v-card-title primary-title>
                <h4>Cadastrar</h4>
                </v-card-title>
                <v-form>
                <v-select style="margin:15px;"
                    v-model="transferencia.usuario"
                    :items="usuarios"
                    item-text="nome"
                    item-value="id"
                    label="Usuário"
                    return-object
                    single-line
                ></v-select>
                <v-select style="margin:15px;"
                    v-model="transferencia.tipo"
                    :items="tipos"
                    item-text="tipo"
                    item-value="id"
                    label="Tipo"
                    persistent-hint
                    return-object
                    single-line
                ></v-select>
                <v-text-field  name="Username" label="Valor" type="number" v-model="transferencia.valor" class="mr-2 ml-2" ></v-text-field>
                <v-card-actions>
                <v-btn primary large block class="blue--text" v-on:click="cadastrar">Cadastrar</v-btn>
                </v-card-actions>
                </v-form>
            </v-card>
            </v-container>
        </v-flex>
        </v-layout>
</template>
<script>
import { auth } from '../firebase'
  export default {
    data: () => ({
      transferencia:{
          tipo: 1,
          usuario:{},
          valor:""
      },
      tipos:[{id:'1', tipo:'Depósito'},{id:'2', tipo:'retirada'}]
    }),
    computed:{
      usuarios(){
          
          return this.$store.getters.usuariosLista
      }
    },
    mounted(){
        auth.onAuthStateChanged((user) =>{
            
        if (user) {
            this.$store.dispatch('usuarios')
               console.log(this.$store.getters.usuariosLista)
            
        } else {
            this.$router.push({
                name: 'login'
            })
        }
        });
    },
    methods:{
        cadastrar(){
            if(this.transferencia.tipo.id == 2){
                this.$store.dispatch('cadastroRetirada', this.transferencia).then(()=>{
                    this.transferencia = {
                        tipo: 1,
                        usuario:{},
                        valor:""
                    }
                })
            }else{
                this.$store.dispatch('cadastroDeposito', this.transferencia)
            }
        }
    }

  }
</script>

<style>
.fundo{
    width:100%;
    background-color: #DCEDC8;
}
.acessar{
    font-size: 12pt;
    text-decoration: none;
}

</style>
