
<template>
        <v-layout row class="text-xs-center fundo">
        <v-flex xs12 class="grey lighten-4">
            <v-container style="position: relative;top: 1%;" class="text-xs-center">
            <v-card flat>
                <v-card-title primary-title>
                <h4>Acessar</h4>
                </v-card-title>
                <v-form>
                <v-text-field prepend-icon="person" name="Username" label="E-mail" class="mr-2 ml-2" v-model="usuario.email"></v-text-field>
                <v-text-field prepend-icon="lock" name="Password" label="Senha" class="mr-2 ml-2" type="password" v-model="usuario.senha"></v-text-field>
                <v-card-actions>
                <v-btn primary large block class="blue--text" @click="logar">Acessar</v-btn>
                </v-card-actions>
                <v-card-actions>
                </v-card-actions>
                </v-form>
            </v-card>
            </v-container>
        </v-flex>
        </v-layout>
</template>
<script>
import {auth} from '../firebase'
  export default {
    data: () => ({
      usuario:{
          email:'',
          senha:''
      }
    }),
    computed:{
      
    },
    created(){
        auth.onAuthStateChanged((user) =>{
            
        if (user) {
            this.$router.push({
                name: 'home'
            })
        } else {

        }
        });
    },
    methods:{
        logar(){
            this.$store.commit('preloader')
            this.login()
            .then(()=>{
                this.$store.commit('preloader')
                this.$snotify.success('Logado com sucesso!')
                this.$router.push({name: 'home'})
            })
            .catch((error)=>{
                 this.$snotify.error(error)
                 console.log(error)
                 this.$store.commit('preloader')
                 this.$router.push({name: 'login'})
            })
        },
        login(){
            return new Promise((resolve, reject)=>{
            auth.signInWithEmailAndPassword(this.usuario.email, this.usuario.senha)
            .then((response)=>{
                var userId = response.user.uid
                let login = ''
                this.$store.dispatch('logarUsuario', userId).then(()=>{
                    resolve()
                }).catch(()=>{
                    let mensagemErro = 'Permissão negada.'
                    reject(mensagemErro)
                })
            })
            .catch(function(error){
                console.log(error.code)
                let mensagemErro = ''
                if(error.code == 'auth/invalid-email'){
                    mensagemErro = "E-mail inválido."
                }else if(error.code == 'auth/user-not-found'){
                    mensagemErro = "Usuário não encontrado"
                }else if(error.code == 'auth/wrong-password'){
                    mensagemErro = "Senha inválida."
                }
                reject(mensagemErro)
           })

        })
        },
    }

  }
</script>

<style>
.fundo{
    width:100%;
    background-color: #DCEDC8;
}

</style>