import Vue from 'vue';
import Vuex from 'vuex';
import {db, auth} from './firebase';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    dataSelecionada:'22/2/2019',
    agendamentoSelecionado: {},
    agendamentos:[],
    usuario:{},
    usuarios:[],
    depositos:[],
    saques:[],
    usuarioAgendamento:{},
    produtos:[],
    preloader: false,
    periodo: 0,
    turno: 1,
    produtoEmUso:[],
    infoUsuario:{
      nome:'',
      email:'',
      turma: 0,
      saldo:''
    }
  },


  mutations: {
    infoUsuario(state, user){
      console.log('commit', user)
      state.infoUsuario ={
        nome: user.nome,
        email: user.email,
        saldo: user.saldo,
        turma: user.turma
      }
    },
    usuarios(state, usuarios){
      state.usuarios = []
      usuarios.forEach((usuario)=>{
        state.usuarios.push({
          key: usuario.key,
          nome: usuario.val().nome,
          email: usuario.val().email,
          turma: usuario.val().turma,
          saldo: usuario.val().saldo
        })
      })
    },
    depositos(state, depositos){
      state.depositos = []
      depositos.forEach((deposito)=>{
        state.depositos.push({
          nomeUsuario: deposito.val().usuario.nome,
          idUsuario: deposito.val().usuario.id,
          valor: deposito.val().valor,
          data: deposito.val().data,
        })
      })
    },
    saques(state, saques){
      state.saques = []
      saques.forEach((saque)=>{
        state.saques.push({
          nomeUsuario: saque.val().usuario.nome,
          idUsuario: saque.val().usuario.id,
          valor: saque.val().valor,
          data: saque.val().data,
        })
      })
    },
    dataSelecionada(state, data){
      state.dataSelecionada = data.data.date
    },
    preloader(state){
      state.preloader = !state.preloader
    },

    agendamentoSelecionado(state, agendamento){

      state.agendamentoSelecionado = {
          data: state.dataSelecionada,
          periodo: agendamento.periodo,
          turma: agendamento.turma,
          produtoId: agendamento.produtoId,
          produtoNome: agendamento.produtoNome,
          turno: agendamento.turno
        }
    },

    meusAgendamentos(state, agendamentos){
      state.agendamentos = []
     if(agendamentos.val() == null){
      state.agendamentos = false
      if(!state.agendamentos){
        
      }
     }else{
      agendamentos.forEach((agendamento)=>{
        state.agendamentos.push({
          id: agendamento.key,
          data: agendamento.val().data,
          periodo: agendamento.val().periodo,
          produto: agendamento.val().produto,
          professor: agendamento.val().professor,
          turma: agendamento.val().turma,
          turno: agendamento.val().turno
        })
      })
     }
      
    },

    Agendamentos(state, agendamentos){
      state.agendamentos = []
     if(agendamentos.val() == null){
      state.agendamentos = false
      if(!state.agendamentos){
        
      }
     }else{
      agendamentos.forEach((data)=>{
        data.forEach((turno)=>{
          turno.forEach((periodo)=>{
            periodo.forEach((agendamento)=>{
            state.agendamentos.push({
              id: agendamento.key,
              date: agendamento.val().data,
              periodo: agendamento.val().periodo,
              produto: agendamento.val().produto.nome,
              professor: agendamento.val().professor.nome,
              turma: agendamento.val().turma,
              turno: agendamento.val().turno
            })
            })
          })
        })
        
      })
     }
      
    },

    produtos(state, produtos){
      state.produtos = []
      
     if(produtos.val() == null){
      state.produtos = false
      
     }else{
      produtos.forEach((produto)=>{
        state.produtos.push({
          id: produto.key,
          img: produto.val().img,
          nome: produto.val().nome,
          descricao: produto.val().descricao,
        })
      })
     }
      
    },

    logarUsuario(state, usuario){
      state.usuario = usuario
      console.log(usuario)
    },
    
    deslogarUsuario(state){
      state.usuario = {}
    },

    usuarioAgendamento(state, usuario){
      state.usuarioAgendamento = usuario
    },
    periodoETurnoSelecionados(state, payload){
      state.periodo = payload.periodo
      state.turno = payload.turno
    },
    produtoEmUso(state, snapshot){
      state.produtoEmUso = []
      snapshot.forEach((produto)=>{
        state.produtoEmUso.push(parseInt(produto.val().produto.id))
      })
    }
  },


  actions: {
    usuario(context){
      let user = []
      db.ref('users/').child(auth.currentUser.uid).on('value', function(snapshot){
        console.log('snapshot',snapshot.val())
        user = {
          nome: snapshot.val().nome,
          saldo: snapshot.val().saldo,
          turma: snapshot.val().turma,
          email: snapshot.val().email
        }
        context.commit('infoUsuario', user)
      })
    },
    usuarios(context){
      
        db.ref('users/').on('value', function(snapshot) {
          context.commit('usuarios', snapshot)

        })
    },
    depositos(context){
        db.ref('depositos/').on('value', function(snapshot) {
          console.log(snapshot.val())
          context.commit('depositos', snapshot)
        })
      
    },
    saques(context){
        db.ref('saques/').on('value', function(snapshot) {
          console.log(snapshot.val())
          context.commit('saques', snapshot)
        })
     
    },
    cadastroRetirada(context, payload){
      return new Promise((resolve, reject)=>{
      db.ref('saques/').push({
        data: "26/4/1994",
        valor: payload.valor,
        usuario: payload.usuario
      }).then((r)=>{
        let user;
        db.ref('users/').child(payload.usuario.id).once('value').then(
          (data)=>{
            user = data.val()
            db.ref('users/').child(payload.usuario.id).update(
              {
                saldo: (user.saldo <= 0) ? 0 : (parseInt(user.saldo) - parseInt(payload.valor))
              }
            ).then((r)=>{
              resolve()
            })
            
          }
        )
      })
      .catch((r)=>{
        console.log("ok")
        reject()
      })
    })
    },
    cadastroDeposito(context, payload){
      db.ref('depositos/').push({
        data: "26/4/1994",
        valor: payload.valor,
        usuario: payload.usuario
      }).then((r)=>{
        let user;
        db.ref('users/').child(payload.usuario.id).once('value').then(
          (data)=>{
            user = data.val()
            db.ref('users/').child(payload.usuario.id).update(
              {
                saldo:(parseInt(user.saldo) + parseInt(payload.valor))
              }
            ).then((r)=>{
              resolve()
            })
          }
        )
      })
    },
    agendamentos(context){
      let agendamentosRef = db.ref('reservaPorData/');
      agendamentosRef.on('value', function(snapshot) {
        
        context.commit('Agendamentos', snapshot);
      });
    },

    produtos(context){
      let produtosRef = db.ref('produtos/')
      produtosRef.on('value', function(snapshot){
        context.commit('produtos', snapshot)
        
      })
    },

    cadastrarUsuario(context, payload){
      return new Promise((resolve, reject) => {
        auth.createUserWithEmailAndPassword(payload.usuario.email, payload.usuario.senha)
        .then((user)=>{
          
          let newUser = {
            id: user.user.uid,
            nome: payload.usuario.nome,
            turma: payload.usuario.turma,
            email: payload.usuario.email,
            admin: (payload.usuario.admin)?1:0,
            saldo: payload.usuario.valor
          }
          db.ref('users/').child(user.user.uid).set(newUser)
          resolve()
        })
        .catch((error)=>{
          reject()
        })
      })
      
    },
    cadastrarProduto(context, payload){
      return new Promise((resolve, reject) => {
        
          db.ref('produtos/').push({
            nome: payload.nome,
            descricao: payload.descricao,
            img: payload.img
          }).then(()=>{
            resolve()
          })
      })      
    },
    logarUsuario(context, uid){
      return new Promise((resolve, reject)=>{
        db.ref('users/' + uid).once('value').then((snapshot) =>{
          console.log(snapshot.val().admin)
          
            var usuario = {
              id: uid,
              nome: snapshot.val().nome,
              email: snapshot.val().email,
            }
            context.commit('logarUsuario', usuario)
            resolve()
        });
      })
    },
    sair(context){
      auth.signOut().then(function() {
        context.commit('deslogarUsuario')
      })
    },
    periodoETurnoSelecionados(context, payload){
      context.commit('periodoETurnoSelecionados', payload)
    },
    produtoEmUso({ commit, state }){
      let data = state.dataSelecionada ? state.dataSelecionada.replace('/','').replace('/', '') : ''
      db.ref('/reservaPorData/' + data).child(state.turno).child(state.periodo).once('value').then(function(snapshot){
        commit('produtoEmUso', snapshot)
      })
    }
  },


  getters:{
    usuarios(state){
      console.log(state.usuarios)
      return state.usuarios
    },
    usuariosLista(state){
      console.log(state.usuarios)
      let lista = []
      state.usuarios.forEach((usuario)=>{
        lista.push({
          nome: usuario.nome,
          id: usuario.key
        })
      })
      return lista
    },
    depositos(state){
      console.log(state.depositos)
      return state.depositos
    },
    saques(state){
      return state.saques
    },
    dataSelecionadaFormatada(state){
      if(state.dataSelecionada == null){
        return null
      }else{
        var today = new Date(state.dataSelecionada);
        var dd = today.getDate(); 
        var mm = today.getMonth()+1; //January is 0! 
        var yyyy = today.getFullYear(); 
        if(dd<10){dd='0'+dd} 
        if(mm<10){mm='0'+mm} 
        var today = dd+'/'+mm+'/'+yyyy;
        var teste = today.split('/') 
        return today
      }
      
      
    },
    agendamentos(state){
      return state.agendamentos
    },
    usuarioLogado(state){
      return state.usuario
    },
    produtosSelect(state){
      let produtosSelect = []

      
      let produtosLivres = []
      function estaEmUso(value){
        if(state.produtoEmUso.indexOf(parseInt(value.id)) != -1){
          
          return false
        }else{
          produtosLivres.push(value)
          return true
        }
      }

      state.produtos.forEach((produto)=>{
        estaEmUso(produto)
      })
      
      produtosLivres.forEach((produto)=>{
        produtosSelect.push({
          nome: produto.nome,
          id: produto.id
        })
      })
      return produtosSelect
    },
    produtos(state){
      return state.produtos
    },
    preloader(state){
      return state.preloader
    },
    infoUsuario(state){
      console.log('getter',state.infoUsuario)
      return state.infoUsuario
    }
  },
});
