
<template>
    <div>
        <div v-if="{depositos}">
        <v-layout row class="text-xs-center fundo"  v-for="(deposito, index) in depositos" :key="index">
        <v-flex xs12 class="grey lighten-4" >
            <v-card
                class="mx-auto"
                color="#26c6da"
                dark
                max-width="400"
            >
                <v-card-title>
                
                <span class="title font-weight-bold">{{deposito.nomeUsuario}}</span>
                </v-card-title>


                <v-card-actions>
                <v-list-tile class="grow">
                    

                    <v-list-tile-content>
                    <v-list-tile-title>Data: {{deposito.data}}</v-list-tile-title>
                    </v-list-tile-content>

                    <v-layout
                    align-center
                    justify-end
                    >
                    <v-icon class="mr-1">mdi-heart</v-icon>
                    <span class="subheading mr-2">Valor</span>
                    <span class="mr-1">·</span>
                    <v-icon class="mr-1">mdi-share-variant</v-icon>
                    <span class="subheading">R${{deposito.valor}}</span>
                    </v-layout>
                </v-list-tile>
                </v-card-actions>
            </v-card>
            <v-divider></v-divider>
        </v-flex>
        </v-layout>
        </div>
        <v-btn
            v-on:click="add"
              absolute
              dark
              fab
              bottom
              right
              color="pink"
            >
              <v-icon>add</v-icon>
            </v-btn>
    </div>
</template>
<script>
import { auth } from '../firebase'
  export default {
    data: () => ({
    }),
    computed:{
     depositos(){
         return this.$store.getters.depositos
     }
    },
    mounted(){
        auth.onAuthStateChanged((user) =>{
            
        if (user) {
            
            this.$store.dispatch('depositos').then(()=>{
               
            })
            
        } else {
            this.$router.push({
                name: 'login'
            })
        }
        });
    },
    methods:{
       add(){
           this.$router.push({"name": "transferencias"})
       }
    }

  }
</script>

<style>
.fundo{
    width:100%;
    background-color: #DCEDC8;
}
.acessar{
    font-size: 12pt;
    text-decoration: none;
}

</style>
