
<template>
        <v-layout row class="text-xs-center fundo">
        <v-flex xs12 class="grey lighten-4">
            <v-container style="position: relative;top: 1%;" class="text-xs-center">
            <v-card flat>
                <v-card-title primary-title>
                <h4>Cadastrar</h4>
                </v-card-title>
                <v-form>
                <v-text-field prepend-icon="person" name="Username" label="Nome" class="mr-2 ml-2" v-model="usuario.nome"></v-text-field>
                <v-text-field prepend-icon="mail" name="Username" label="E-mail" class="mr-2 ml-2" v-model="usuario.email"></v-text-field>
                <v-text-field prepend-icon="lock" name="Password" label="Senha" class="mr-2 ml-2" type="password" v-model="usuario.senha"></v-text-field>
                <v-text-field prepend-icon="book" name="Username" label="Turma" class="mr-2 ml-2" type="number" v-model="usuario.turma"></v-text-field>
                <v-text-field prepend-icon="attach_money" name="Username" label="Valor Inicial" class="mr-2 ml-2" type="number" v-model="usuario.valor"></v-text-field>
                <v-card-actions>
                <v-btn primary large block class="blue--text" v-on:click="cadastrar">Cadastrar</v-btn>
                </v-card-actions>
                </v-form>
            </v-card>
            </v-container>
        </v-flex>
        </v-layout>
</template>
<script>
import { auth } from '../firebase'
  export default {
    data: () => ({
      usuario:{
          nome:'',
          email:'',
          senha: '',
          turma: 0,
          valor: 0
      },
    }),
    computed:{
     
    },
    mounted(){
        auth.onAuthStateChanged((user) =>{
            console.log('user', user)
        if (user) {
            
        } else {
            this.$router.push({
                name: 'login'
            })
        }
        });
    },
    methods:{
        cadastrar(){
            this.$store.commit('preloader')
            this.$store.dispatch('cadastrarUsuario',{
                usuario: this.usuario
            })
            .then(()=>{
                this.$snotify.success('Sucesso ao cadastrar')
                this.usuario ={
                        nome:'',
                        email:'',
                        senha: '',
                        turma: 0,
                        valor: 0
                }
                this.$store.commit('preloader')
            })
            .catch(()=>{
                this.$snotify.error('Erro ao cadastrar, tente mais tarde!')
                this.$store.commit('preloader')
            })
        }
    }

  }
</script>

<style>
.fundo{
    width:100%;
    background-color: #DCEDC8;
}
.acessar{
    font-size: 12pt;
    text-decoration: none;
}

</style>
