import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';

import CadastroUsuario from './views/CadastroUsuario';
import Login from './views/Login';
import Usuarios from './views/Usuarios';
import Depositos from './views/Depositos';
import Saques from './views/Saques';
import Transferencias from './views/Transferencias';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: '/',
      component: Login,
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },
    
    {
      path: '/saques',
      name: 'saques',
      component: Saques,
    },
    {
      path: '/depositos',
      name: 'depositos',
      component: Depositos,
    },
    {
      path: '/transferencia',
      name: 'transferencias',
      component: Transferencias,
    },
    {
      path: '/usuarios',
      name: 'usuarios',
      component: Usuarios,
    },
    {
      path: '/cadastro-usuario',
      name: 'cadastroUsuario',
      component: CadastroUsuario,
    },
    
  ],
});
